.comics {
    &__list {
        margin-top: 45px;
    }
    &__grid {
        display: grid;
        grid-template-columns: repeat(auto-fill, 225px);
        grid-auto-rows: auto; 
        justify-content: space-between;
        row-gap: 55px;
    }
    &__item {
        opacity: 0;
        transform: translateY(-20%);
        visibility: hidden;
        &-img {
            box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.25);
            width: 225px;
            height: 300px;
        }
        &-name {
            margin-top: 10px;
            font-weight: bold;
            font-size: 14px;
            line-height: 18px;
        }
        &-price {
            margin-top: 5px;
            font-weight: bold;
            font-size: 14px;
            line-height: 18px;
            color: rgba(0, 0, 0, 0.6);
            text-transform: uppercase;
        }
        &:hover {
            transform: translateY(-5px);
        }
        &.in-view {
            transition: all 0.3s ease-in-out; 
            opacity: 1;
            visibility: visible;
            transform: translateY(0); /* Apply your desired animation properties here */
        }
    }
}

